html {
    scroll-behavior: smooth;
}

@media (max-width: 768px) {
    .card_container {
        flex-direction: column;
    }
}

.card-img-top {
    width: 10rem;
    height: 10rem;
    margin: auto;
    margin-top: 1rem;
    border: 2px solid red;
    border-radius: 50%;
    overflow: visible;
    padding: 0.8rem 0.8rem;
}

.btn-myDanger {
    background-color: red;
    padding: 0.5rem;
    color: white;
    width: 8rem;
    margin: auto;
    margin-bottom: 1rem;
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
}
.btn-myDanger:hover {
    background-color: rgb(221, 11, 11);
    color: white;
}
.btn-myDanger:active {
    background-color: rgb(221, 11, 11);
    color: white;
}

/* Searchbar styling */
.search {
    width: 100%;
    margin-bottom: auto;
    margin-top: 1rem;
    height: 4rem;
    background-color: #f3f1f1;
    padding: 10px;
    border-radius: 5px;
    border: .5px solid gray
}

.search-input {
    color: white;
    border: 0;
    outline: 0;
    background: none;
    width: 0;
    margin-top: 5px;
    caret-color: transparent;
    line-height: 30px;
}

.search .search-input {
    padding: 0 10px;
    width: 100%;
    caret-color: #536bf6;
    font-size: 1.5rem;
    font-weight: 300;
    color: black;
}

.search-icon {
    height: 3rem;
    width: 3rem;
    float: right;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    bottom: 37px;
    position: relative;
    border-radius: 1.5rem;
    border: none
}

a:link {
    text-decoration: none;
}

.card-inner {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0, 0, 0, 0.125);
    border-radius: 0.25rem;
    border: none;
    cursor: pointer;
}

.mg-text span {
    font-size: 12px;
}

.mg-text {
    line-height: 14px;
}
